<template>
  <div
    ref="wrapper"
    class="drag-bar-wrapper"
  >
    <div
      ref="header"
      class="drag-bar-header"
    >
      <!-- 头部区域 -->
      <slot name="header" />
    </div>
    <div class="drag-bar-content">
      <!-- 主内容区域 -->
      <slot name="default" />
    </div>
    <div class="drag-bar-footer">
      <!-- 底部区域 -->
      <slot name="footer" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'MoveBox',
  data() {
    return {
      wrapperDom: null,
      headerDom: null,

      disX: 0,
      disY: 0,

      maxLeft: 0,

      maxTop: 0,

      ml: 0, // marginLeft
      mr: 0, // marginRight
      mlr: 0,
      mt: 0, // marginTop
      mb: 0, // marginBottom
      mtb: 0,
    }
  },
  mounted() {
    this.initDrag()
    window.onresize = () => {
      return (() => {
        let DOMRect = this.wrapperDom.getBoundingClientRect()
        let t = window.innerWidth - DOMRect.right - this.mr
        if( t < 0 && window.innerWidth > DOMRect.width + this.mlr) {
          this.wrapperDom.style.left = DOMRect.left - this.ml + t + 'px'
        }
        let l = window.innerHeight - DOMRect.bottom - this.mb
        if( l  < 0 && window.innerHeight > DOMRect.height + this.mtb) {
          this.wrapperDom.style.top = DOMRect.top - this.mt + l  + 'px'
        }
      })();
    };
  },
  methods: {
    initDrag() {
      this.wrapperDom = this.$refs.wrapper
      this.headerDom = this.$refs.header

      const style = getComputedStyle(this.wrapperDom, false) // 获取margin
      this.ml = parseFloat(style.marginLeft)
      this.mr = parseFloat(style.marginRight)
      this.mlr = this.ml + this.mr
      this.mt = parseFloat(style.marginTop)
      this.mb = parseFloat(style.marginBottom)
      this.mtb = this.mt + this.mb

      this.headerDom.addEventListener('mousedown', this.onMousedown, false) // 点击头部区域拖拽
    },
    onMousedown(e) {
      let DOMRect = this.wrapperDom.getBoundingClientRect()

      this.disX = e.clientX - DOMRect.left
      this.disY = e.clientY - DOMRect.top

      this.maxLeft = window.innerWidth - DOMRect.width - this.mlr
      this.maxTop = window.innerHeight - DOMRect.height - this.mtb
      document.addEventListener('mousemove', this.onMousemove, false)
      document.addEventListener('mouseup', this.onMouseup, false)
      document.body.style.userSelect = 'none' // 消除拖拽中选中文本干扰
    },
    onMousemove(e) {
      this.headerDom.style.cursor = 'move'
      document.body.style.cursor = 'move'

      let left = e.clientX - this.disX - this.ml // 主窗X轴
      let top = e.clientY - this.disY - this.mt // 主窗Y轴

      if (left < 0) {
        left = 0
      } else if (left > this.maxLeft) {
        left = this.maxLeft
      }

      if (top < 0) {
        top = 0
      } else if (top > this.maxTop) {
        top = this.maxTop
      }

      this.wrapperDom.style.left = left + 'px'
      this.wrapperDom.style.top = top + 'px'
    },
    onMouseup() {
      this.headerDom.style.cursor = 'default'
      document.body.style.cursor = 'default'
      document.removeEventListener('mousemove', this.onMousemove, false)
      document.removeEventListener('mouseup', this.onMouseup, false)
      document.body.style.userSelect = 'auto' // 恢复文本可选中
    }
  }
}
</script>

<style scoped>
  .drag-bar-wrapper {
    position: fixed;
    z-index: 2;
    display: flex;
    flex-direction: column;
  }
  .drag-bar-header {
    background-color: #eee;
    /*cursor: move; !*拖拽鼠标样式*!*/
  }
  .drag-bar-content {
    background-color: #fff;
  }
  .drag-bar-footer {
    background-color: #fff;
  }
</style>
